import React from 'react';
import styled, {css} from 'styled-components';
import Bus from './Bus';
import Train from './Train';
import Island from './Island';
import Jeju from './Jeju';
import Free from './Free';
import City from './City';

const MenuListBlock = styled.div`
    .tabs { display: flex; justify-content: center;
        div:hover { color: #047be0; transition: all 0.3s; }
    }
`;

const List = styled.div`
    cursor: pointer;
    padding: 10px 50px;
    background: #fff;
    border: 1px solid #ddd;
    ${
        props => props.active && css`
            background: #333; color: #fff;
        `
    }
`;

const obj = [
    { id:0, title:"버스여행", tabCont:<Bus /> },
    { id:1, title:"기차여행", tabCont:<Train /> },
    { id:2, title:"섬여행", tabCont:<Island /> },
    { id:3, title:"제주여행", tabCont:<Jeju /> },
    { id:4, title:"자유여행", tabCont:<Free /> },
    { id:5, title:"시티투어", tabCont:<City /> },
]

const MenuList = ({activeTab, onSelect}) => {
    return (
        <MenuListBlock>
            <div className="row">
                <div className="tabs">
                    {
                        obj.map( (data) => (
                        <List key={data.id} active={activeTab === data.id} onClick={ () => onSelect(data.id) }>{data.title}</List>
                        ) )
                    }
                </div>
                <div className="contents">
                    {
                        obj[activeTab].tabCont
                    }
                </div>
            </div>
        </MenuListBlock>
    );
};

export default MenuList;