import React from 'react';
import MemberTemp from '../components/MemberTemp';
import MemberForm from '../components/MemberForm';
import styled from 'styled-components';

const LoginImgBlock = styled.div`
    position: relative; top: 64px;
    text-align: center;
`;

const Login = () => {
    return (
        <MemberTemp>
            <MemberForm type="login" />
            <LoginImgBlock>
              <img src="images/go_world.png" />
              <img src="images/go_bus.png" />
              <img src="images/go_airplane.png" />
            </LoginImgBlock>
        </MemberTemp>
    );
};

export default Login;
