import React from 'react';
import MemberTemp from '../components/MemberTemp';
import MemberForm from '../components/MemberForm';
import styled from 'styled-components';

const JoinImgBlock = styled.div`
    position: relative; top: 64px;
    text-align: center;
`;

const Join = () => {
    return (
        <MemberTemp>
          <MemberForm type="join" />
          <JoinImgBlock>
            <img src="images/go_world.png" />
            <img src="images/go_bus.png" />
            <img src="images/go_airplane.png" />
          </JoinImgBlock>
        </MemberTemp>
    );
};

export default Join;